.display-picture {
  padding-top: 70px; }

.hi {
  position: absolute;
  text-decoration: none;
  color: #000;
  letter-spacing: 2px;
  padding: 50px 100px;
  font-size: 100px;
  font-weight: 500;
  font-family: "Work Sans", sans-serif;
  -webkit-transition: opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  -o-transition: transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  -webkit-transform: translateY(100%) scale3d(1.2, 1.2, 1.2);
          transform: translateY(100%) scale3d(1.2, 1.2, 1.2);
  opacity: 0; }

.hi-animate-appear {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  opacity: 0.02; }

.hi-animate-appear.hi-animate-appear-active {
  -webkit-transition: opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  -o-transition: transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, opacity 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 1s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  -webkit-transform: translateY(100%) scale3d(1.2, 1.2, 1.2);
          transform: translateY(100%) scale3d(1.2, 1.2, 1.2);
  opacity: 1; }

.my-logo {
  font-family: 'Fugaz One',sans-serif,cursive;
  font-size: 140px;
  font-weight: 700;
  text-align: center;
  display: block;
  width: 50%;
  margin: 0 auto;
  height: auto; }

.my-picture {
  display: block;
  width: 70%;
  margin: 0 auto;
  height: auto; }

@media only screen and (max-width: 850px) {
  .hi {
    display: none; }
  .my-picture {
    width: 75%; }
  .my-logo {
    font-size: 70px;
    width: 100%; } }



