.trans-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: fixed;
  width: 100%;
  z-index: 1000; }

.logo {
  -ms-flex: 1 1;
      flex: 1 1;
  padding-top: 5px;
  padding-left: 20px; }

.logo a {
  text-decoration: none;
  font-family: Raleway, sans-serif;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
