.iphone-ad {
  padding-top: 100px;
  background-color: #F9F8FF;
  height: 700px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center; }

.iphone {
  height: 530px; }

.iphone-ad .caption {
  font-family: "Work Sans", sans-serif;
  font-size: 5em;
  color: black;
  -webkit-animation: hideshow 7s ease infinite;
          animation: hideshow 7s ease infinite; }

.iphone-ad .caption p {
  font-size: 0.4em;
  line-height: 1.5em;
  margin-bottom: 18px; }

.strike-through {
