@-webkit-keyframes hideshow {
  0% {
    opacity: 0; }
  10% {
    opacity: 0; }
  15% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes hideshow {
  0% {
    opacity: 0; }
  10% {
    opacity: 0; }
  15% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@media only screen and (max-width: 705px) {
